.vegee {
  position: relative;
  text-transform: lowercase;
  font-weight: 600;
}
.bhs,
.bhsInner,
.vegeeWrapper {
  overflow: hidden;
  display: flex;
  flex-direction: column;
}
.vegeeWrapper {
  border-radius: var(--br-3xs);
  align-items: flex-start;
  justify-content: center;
  padding: var(--padding-3xs) var(--padding-8xs);
}
.bhs,
.bhsInner {
  justify-content: flex-start;
}
.bhsInner {
  align-self: stretch;
  align-items: center;
  padding: var(--padding-3xs);
  font-size: var(--font-size-xs);
  font-family: var(--font-reem-kufi);
}
.bhs {
  width: 100%;
  position: relative;
  background-color: var(--color-whitesmoke);
  align-items: flex-start;
  padding: var(--padding-31xl) var(--padding-3xs) var(--padding-xl);
  box-sizing: border-box;
  gap: var(--gap-11xl);
  opacity: 0;
  text-align: center;
  font-size: var(--font-size-36xl);
  color: var(--color-black);
  font-family: var(--font-tourney);
}
.bhs.animate {
  animation: 1s ease 0s 1 normal forwards fade-in;
}
